.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  //background-color: teal;
  width: 100%;
  padding: 10px 10px;
  border-radius: 5px;
  background-color: #d0d0d0;
  color: black;
  font-size: 18px;
  transition: all 0.3s ease;
  z-index: 3;
  //background-color: red;

  &:hover {
    transition: all 0.3s ease;
    //background-color: #396aab;
    box-shadow: 0 4px 6px rgba(57, 106, 171, 0.5), 0 1px 2px rgba(255, 255, 255, 0.1);
  }

  p {
    //border: 1px solid black;
    width: 100%;
    height: 100%;
    //text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default;
  }

  button {
    background-color: #396aab;
    color: white;
    font-size: 16px;
    padding: 10px 25px;
    transition: all 0.3s ease;

    &:hover {
      //padding: 11px 26px;
      //font-size: 17px;
      //box-shadow: 0 4px 6px rgba(57, 106, 171, 0.5), 0 1px 2px rgba(255, 255, 255, 0.1);
      transition: all 0.3s ease;
    }
  }
}

